import React, { useState, useEffect } from 'react';
import NewsCard from './NewsCard'
import data from '../data'
import './News.css'

function News() {
    const [items, setItems] = useState([]); 
     
     useEffect(() => {
        const currentDate = new Date().toJSON().slice(0,10);
        fetch("https://ft5f6uj1n2.execute-api.eu-west-3.amazonaws.com/Prod/"+currentDate)
        .then(res=> res.json())
        .then(setItems);
    }, []);
      
    return (
        <div className="news-flex">
            {items.map((item) => 
                (
                    <NewsCard key={item.uuid} title={item.title} description={item.contentSnippet} link={item.link} />
                )
            )}
        </div>
    )
}

export default News


