import React from 'react';

function NewsCard(props){

    function toSnippet(content, maxLength) {
        return content.length >= maxLength? content.substring(0, maxLength-3) + "..." : content;
    } 

    return (
        <div onClick={() => window.open(props.link,'_blank')} className="clickable news-card-flex">
            <div className="news-content">
                <h3><a href={props.link} target="_blank" rel="noopener noreferrer">{props.title}</a></h3>
                {toSnippet(props.description, 150)}
            </div>
            <div className="news-img">
                <img src ="https://via.placeholder.com/120" alt="placeHolder"></img>
            </div>
        </div>
    )
}



export default NewsCard